.fridge {
    width: 30vw;
    height: 70vh;
    border: 10px;
    margin-left: auto;
    margin-right: auto;
    margin-top: auto;
    margin-bottom: auto;
    display: block;
    max-width: 300px;
}


.fridge-container {
    position: relative;
    cursor:pointer;
}
.fridge-inline {
    position: absolute;
    top: 40px;
    left: 40px;
  }